import React from 'react';

import {Route, Link, HashRouter, Switch} from 'react-router-dom';
import OtherPlanetEclipseAnalyzer from './OtherPlanetEclipseAnalyzer.js';
import ApparentSizeVariance from './ApparentSizeVariance.js';
import RecessionOfMoon from './RecessionOfMoon.js';
import Polaris from './Polaris.js';
import Images from './Images.js';
// import EarthMoonUmbra from './EarthMoonUmbra';


// I wanted to put this entire app on a github site at /astroTools, but it didn't work.
// I think this may be solvable by prefixing the URLs with the name of the folder.
const URL_PREFIX = '/astro-tools';

function App() {
  return (<div>
    <HashRouter basename={URL_PREFIX}>
      <Switch>
        <Route path='/OtherPlanetEclipseAnalyzer'>
          <Link to='/eclipse'>Back to eclipse coincidence analysis tools</Link>
          <OtherPlanetEclipseAnalyzer/>
        </Route>
          
        <Route path='/ApparentSizeVariance'>
          <Link to='/eclipse'>Back to eclipse coincidence analysis tools</Link>
          <ApparentSizeVariance/>
        </Route>
        <Route path='/RecessionOfMoon'>
          <Link to='/eclipse'>Back to eclipse coincidence analysis tools</Link>
          <RecessionOfMoon/>
        </Route>
        <Route path="/eclipse">
          <h1>The Solar Eclipse Coincidence</h1>
          <p>It is a major coincidence that the moon looks almost the exact same size as the sun in earth's sky.  This has major implications for the quality of solar eclipses that occur on earth.  This quality of eclipses is unique to planet earth.  This is interesting since no other planet has intelligent life to observe such a phenomenon.  I was fortunate enough to witness a total solar eclipse on August 21, 2017.</p>
          <p>On average, the angular radius of the moon is about 98% of angular radius of the sun when both are visible.  However, it varies from about 91% to about 106%.  Total eclipses are only possible when it is greater than 100%, which is slightly less than half the time.</p>
          <p>I made some tools to analyze this coincidence.  Click one of these links to go to the tool.</p>
          <Link to='/OtherPlanetEclipseAnalyzer'>Other Planet Eclipse Analyzer</Link><br />
          <Link to='/ApparentSizeVariance'>Maximum and minimum size comparison</Link><br />
          <Link to='/RecessionOfMoon'>Recession of Moon</Link>
        </Route>
        <Route path="/polaris">
          <Polaris/>
        </Route>
        <Route path="/images">
          <Images/>
        </Route>
        <Route path="/">
          <a href={process.env.PUBLIC_URL + '/plainHtmlStuff'}>Moon Coincidence Finder</a><br />
          <a href={process.env.PUBLIC_URL + '/plainHtmlStuff?tool=bodesSolver'}>Titius-Bode Solver</a><br />
          <Link to="/eclipse">The solar eclipse coincidence</Link><br />
          <Link to="/polaris">The North Pole Star (Polaris) Coincidence</Link><br />
          <Link to="/images">Some Telescope Images by Me</Link>
        </Route>
      </Switch>
    </HashRouter>
    {/* <EarthMoonUmbra /> */}
  </div>);
}

export default App;
