import React from 'react';
import {Planet, Star, Moon, moon_data, AstroObject} from './data.js';
import SizeComparison from './SizeComparison.js';

const TRAPPIST_1_RADIUS_SUN_RADII = 0.121;
const SUN_RADIUS_KM = 6.957e5;
const TRAPPIST_1_DATA = [
  // [name, semimajor axis in AU, radius in earth radii]
  ['b', 0.01154775, 1.121],
  ['c', 0.01581512, 1.095],
  ['d', 0.02228038, 0.784],
  ['e', 0.02928285, 0.910],
  ['f', 0.03853361, 1.046],
  ['g', 0.04687692, 1.148],
  ['h', 0.06193488, 0.773]
];
const EARTH_RADIUS_KM = 6378.1;
const AU_KM = 1.496e8;
function au_from_earth_radii(value_in_earth_radii) {
  return value_in_earth_radii * EARTH_RADIUS_KM / AU_KM;
}
function au_from_sun_radii(value_in_sun_radii) {
  return value_in_sun_radii * SUN_RADIUS_KM / AU_KM;
}


function MyTh(props) {
  return <th style={{border: "1px solid black"}}>{props.children}</th>;
}
function MyTd(props) {
  return <td style={{border: "1px solid black"}}>{props.children}</td>;
}
function TitleCase(props) {
  return <span style={{textTransform: 'capitalize'}}>{props.children}</span>;
}

export default function OtherPlanetEclipseAnalyzer() {
  const computed_data = [];
  moon_data["name"].forEach(name => {
    const d = {};
    const moon = new Moon(name);
    const planet = new Planet(moon.planet.value);
    const star = new Star(planet.star.value);
    d.relationship = "moon";
    d.star_color = "yellow";
    d.moon = moon;
    d.moon_apparent_size_from_planet_center = Math.atan(moon["radius (km)"].value / AstroObject.get_base_unit_value("semimajor axis (10^3 km)", moon["semimajor axis (10^3 km)"].value));
    d.moon_apparent_size_from_planet_point_closest_to_moon = Math.atan(moon["radius (km)"].value / (AstroObject.get_base_unit_value("semimajor axis (10^3 km)", moon["semimajor axis (10^3 km)"].value) - planet["equatorial radius (km)"].value));
    d.sun_apparent_size_from_planet_center = Math.atan(star["radius (km)"].value / AstroObject.get_base_unit_value("semimajor axis (10^6 km)", planet["semimajor axis (10^6 km)"].value));
    d.sun_apparent_size_from_planet_point_closest_to_sun = Math.atan(star["radius (km)"].value / (AstroObject.get_base_unit_value("semimajor axis (10^6 km)", planet["semimajor axis (10^6 km)"].value) - planet["equatorial radius (km)"].value));
    d.ratio_from_planet_center = d.moon_apparent_size_from_planet_center / d.sun_apparent_size_from_planet_center;
    d.ratio_from_planet_point_closest_to_moon = d.moon_apparent_size_from_planet_point_closest_to_moon / d.sun_apparent_size_from_planet_point_closest_to_sun;

    d.min_error = Math.min(Math.abs(Math.log(d.ratio_from_planet_center)), Math.abs(Math.log(d.ratio_from_planet_point_closest_to_moon)));

    computed_data.push(d);
  });
  for(let k = 0; k < TRAPPIST_1_DATA.length; k++) {
    for(let j = 0; j < k; j++) {
      const moon_data = TRAPPIST_1_DATA[j];
      const home_planet_data = TRAPPIST_1_DATA[k];
      const d = {};
      d.relationship = "neighbor";
      d.star_color = "red";
      d.moon = {name: {value: "TRAPPIST-1" + moon_data[0]}, planet: {value: "TRAPPIST-1" + home_planet_data[0]}};
      const planet_alignment_distance_au = home_planet_data[1] - moon_data[1];
      const home_radius_au = au_from_earth_radii(home_planet_data[2])
      const moon_radius_au = au_from_earth_radii(moon_data[2]);
      const star_radius_au = au_from_sun_radii(TRAPPIST_1_RADIUS_SUN_RADII);
      d.moon_apparent_size_from_planet_center =                Math.atan(moon_radius_au / planet_alignment_distance_au);
      d.moon_apparent_size_from_planet_point_closest_to_moon = Math.atan(moon_radius_au / (planet_alignment_distance_au - home_radius_au));
      d.sun_apparent_size_from_planet_center =                 Math.atan(star_radius_au / home_planet_data[1]);
      d.sun_apparent_size_from_planet_point_closest_to_sun =   Math.atan(star_radius_au / (home_planet_data[1] - home_radius_au));
      d.ratio_from_planet_center = d.moon_apparent_size_from_planet_center / d.sun_apparent_size_from_planet_center;
      d.ratio_from_planet_point_closest_to_moon = d.moon_apparent_size_from_planet_point_closest_to_moon / d.sun_apparent_size_from_planet_point_closest_to_sun;

      d.min_error = Math.min(Math.abs(Math.log(d.ratio_from_planet_center)), Math.abs(Math.log(d.ratio_from_planet_point_closest_to_moon)));

      computed_data.push(d);
    }
  }
  computed_data.sort((a, b) => a.min_error - b.min_error);
  return (<div>
    <table>
      <thead>
        <tr>
          <MyTh>Related Objects</MyTh>
          <MyTh>From Planet Center</MyTh>
          <MyTh>From Planet Closest Point To Moon</MyTh>
        </tr>
      </thead>
      <tbody>
        {computed_data.map(data => {
          return (<tr key={data.moon.name.value + ":" + data.moon.planet.value} >
            <MyTd>
              <TitleCase>{data.moon.name.value}</TitleCase><br />
              {data.relationship + " of "}<TitleCase>{data.moon.planet.value}</TitleCase>
            </MyTd>
            <MyTd>
              <div style={{display: "flex", alignItems: "center"}}>
                <SizeComparison
                  moonSize={data.moon_apparent_size_from_planet_center}
                  sunSize={data.sun_apparent_size_from_planet_center}
                  starColor={data.star_color}
                />
                <ul>
                  <li><TitleCase>{data.relationship}</TitleCase> angular diameter: {(data.moon_apparent_size_from_planet_center * 2 * 180 / Math.PI).toFixed(3)}°</li>
                  <li>Star angular diameter: {(data.sun_apparent_size_from_planet_center * 2 * 180 / Math.PI).toFixed(3)}°</li>
                  <li>Ratio: {(data.moon_apparent_size_from_planet_center / data.sun_apparent_size_from_planet_center).toFixed(3)}</li>
                </ul>
              </div>
            </MyTd>
            <MyTd>
              <div style={{display: "flex", alignItems: "center"}}>
                <SizeComparison
                  moonSize={data.moon_apparent_size_from_planet_point_closest_to_moon}
                  sunSize={data.sun_apparent_size_from_planet_point_closest_to_sun}
                  starColor={data.star_color}
                />
                <ul>
                  <li><TitleCase>{data.relationship}</TitleCase> angular diameter: {(data.moon_apparent_size_from_planet_point_closest_to_moon * 2 * 180 / Math.PI).toFixed(3)}°</li>
                  <li>Star angular diameter: {(data.sun_apparent_size_from_planet_point_closest_to_sun * 2 * 180 / Math.PI).toFixed(3)}°</li>
                  <li>Ratio: {(data.moon_apparent_size_from_planet_point_closest_to_moon / data.sun_apparent_size_from_planet_point_closest_to_sun).toFixed(3)}</li>
                </ul>
              </div>
            </MyTd>
          </tr>);
        })}
      </tbody>
    </table>
  </div>);
}
