import React from 'react';


class HelpContent extends React.Component {
    // The only props `content`, the content to display if the help button is clicked.  This may be a string or it may be JSX.
    constructor(props) {
        super(props);
        this.state = {
            is_showing_text: false
        };
    }
    render() {
        return (
            <>
                <button onClick={() => {
                    this.setState({
                        is_showing_text: !this.state.is_showing_text
                    });
                }}>?</button>
                {this.state.is_showing_text ? (<><br />{this.props.content}</>) : null}
            </>
        );
    }
}
function PrettyBox(props) {
    return (<div style={{border: "2px solid black", borderRadius: 5, margin: 2}}>
        {props.children}
    </div>);
}

export default class Polaris extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            rank: 50,
            distance_from_pole_degrees: 0.75,
            distance_exponent_in_quality: 2,
            num_stars_limit: 5000,
            num_planets_to_simulate: 10000,
            include_both_poles: false,

            result: null
        };
    }
    getProbabilityBySimulation(num_rounds, include_both_poles, quality_function) {
        // num_rounds: the number of rounds to run to get the probability.  Go at least 10000 for accuracy.
      
        // include_both_poles: boolean
        //  if true, stars near either pole will be considered
        //  if false, stars near only one pole will be considered even though stars in both hemispheres are included in the ranking of stars
      
        // quality_function: (rank, radius) => Number
        //   This function is called for each star.
        //   The higher the number, the better the star is as a pole star.
      
        const polaris_quality = quality_function(this.state.rank, this.state.distance_from_pole_degrees * Math.PI / 180);
      
        let num_rounds_with_better_pole_star_than_polaris = 0;
        for(let round_index = 0; round_index < num_rounds; round_index++) {
          let found_better_pole_star = false;
          for(let star_index = 0; star_index < this.state.num_stars_limit; star_index++) {
            let x = 0;
            let y = 0;
            let z = 0;
            let distance_squared = 0;
            while(x*y*z === 0 || distance_squared >= 1) {
              x = (Math.random() * 2) - 1;
              y = (Math.random() * 2) - 1;
              z = (Math.random() * 2) - 1;
              distance_squared = (x*x) + (y*y) + (z*z);
            }
            // <x, y, z> is now guaranteed to be in the unit sphere.
            // Also, x, y, and z are guaranteed to be nonzero.
      
            if(include_both_poles || z > 0) {
              const quality = quality_function(star_index + 1, Math.atan2(Math.sqrt((x * x) + (y * y)), Math.abs(z)));
              if(quality >= polaris_quality) {
                found_better_pole_star = true;
                // console.log(x, y, z);
                break;
              }
            }
          }
          if(found_better_pole_star) {
            num_rounds_with_better_pole_star_than_polaris++;
          }
        }
      
        return num_rounds_with_better_pole_star_than_polaris * 1.0 / num_rounds;
    }
    getProbabilityByCalculation(include_both_poles, N) {
        // include_both_poles: boolean
        //  if true, stars near either pole will be considered
        //  if false, stars near only one pole will be considered even though stars in both hemispheres are included in the ranking of stars
      
        // N: Number
        //   The pole star quality function must be 1 / (rank * distance_from_pole^N)
      
        const r_p = this.state.distance_from_pole_degrees * Math.PI / 180;
        let p_not = 1;
        for(let rank = 1; rank <= this.state.num_stars_limit; rank++) {
            const max_dist_from_pole = Math.pow(this.state.rank * Math.pow(r_p, N) / rank, 1.0 / N);
            let area_within_max_dist = 2 * Math.PI * (1 - Math.cos(max_dist_from_pole));
            if(include_both_poles) {
                area_within_max_dist *= 2;
            }
            p_not *= 1 - (area_within_max_dist / (4 * Math.PI));
        }
        return 1 - p_not;
    }
    setStateWithResultReset(attrsToSet) {
        const attrs = {};
        for(let k in attrsToSet) {
            attrs[k] = attrsToSet[k];
        }
        attrs.result = null;
        this.setState(attrs);
    }
    runSimulation() {
        this.setState({
            result: "running simulation ... this usually takes less than 30 seconds"
        });
        setTimeout(() => {
            this.setState({
                result: {
                    sim: this.getProbabilityBySimulation(
                        this.state.num_planets_to_simulate,
                        this.state.include_both_poles,
                        (rank, radius) => {
                            return 1 / (rank * Math.pow(radius, this.state.distance_exponent_in_quality));
                        }
                    ),
                    calc: this.getProbabilityByCalculation(
                        this.state.include_both_poles,
                        this.state.distance_exponent_in_quality
                    )
                }
            });
        }, 100);
    }
    getRangeOption(state_key, text_to_display, help_content, min, max, step) {
        return (
            <PrettyBox>
                {text_to_display}<HelpContent content={help_content} /><br />
                <input
                    type="range"
                    style={{width: "95%"}}
                    onChange={event => {
                        this.setStateWithResultReset({
                            [state_key]: Number(event.target.value)
                        });
                    }}
                    value={this.state[state_key]}
                    min={min}
                    max={max}
                    step={step}
                /><br />
                {this.state[state_key]}
            </PrettyBox>
        );
    }
    render() {
        return (
            <div>
                <h3>Probability of Polaris</h3>
                <h4>Background</h4>
                <p>
                    Astronomical objects can be thought of as points on a giant sphere called the <a href="https://en.wikipedia.org/wiki/Celestial_sphere">celestial sphere</a>.
                    This sphere rotates roughly once per day as the earth spins.
                    There are 2 points on the sphere that are not moved by this rotation: <a href="https://en.wikipedia.org/wiki/Celestial_pole">the celestial poles</a>.
                    There happens to be an easily visible star very close to the north celestial pole.
                    Because it is so close to the pole, this star is named Polaris.{" "}
                    <a href="https://en.wikipedia.org/wiki/Polaris">Polaris</a> is roughly the 50th brightest star in the sky (<a href="https://en.wikipedia.org/wiki/List_of_brightest_stars">source</a>), and it is currently about 0.75 degrees away from the north celestial pole.
                    The position of the pole moves due to <a href="https://en.wikipedia.org/wiki/Axial_precession">axial precession</a>.
                    According to <a href="https://earthsky.org/brightest-stars/polaris-the-present-day-north-star">this source</a>, the north celestial pole will come closest to Polaris on March 24, 2100, when the separation will reach its minimum of 0.4245 degrees.
                </p>
                <h4>About this Simulation</h4>
                <p>
                    I was curious about the probability of having pole star with the quality of Polaris.
                    After struggling with mathematics for a long time, I finally came up with a computer simulation that can provide a meaningful number.
                    This simulation works by generating a lot of hypothetical planets.
                    For each simulated planet, the simulation generates a number of random stars, each in a random direction from the planet.
                    The distribution of the stars is spherical.
                    For each star, the simulation calculates a number indicating the <i>pole star quality</i> (or <i>quality</i>) of that star.
                    The higher the quality, the better the star is as a pole star.
                    The simulation counts the number of planets that have at least one star of higher quality than Polaris.
                    If you want to see the code, check out <a href={process.env.PUBLIC_URL + "/polaris.py"}>the python implementation</a>.
                </p>
                <p>
                    This simulation uses the formula <i>quality = 1 / (r * d<sup>n</sup>)</i> where r is the brighness rank of the star (for Polaris it is 50), d is the distance from the pole (for Polaris it is 0.75 degrees), and n is a simulation parameter.
                    If n=1, then if the brightest star in the sky was 30° from the pole, it would be a better pole star than Polaris.
                    So I like to use n=2, but you should try changing n to see what happens.
                </p>
                <p>
                    After I got the simulation to work, I found a way to mathematically calculate the probability of having a better pole star than Polaris.  The calculation requires a computer but it is much faster than the simulation and it gets the exact value.  This tool shows results from both the calculation and the simulation, so you can see that they match.  The calculated result is more accurate.
                </p>
                <h4>Findings</h4>
                <p>
                    The probability of having a better pole star than Polaris is less than 5%.  About 1 in 30 worlds has a better pole star than Polaris.
                </p>
                <h4>The Tool</h4>
                <div style={{textAlign:'left'}}>
                    {this.getRangeOption(
                        "distance_exponent_in_quality",
                        "n (distance exponent in quality denominator)",
                        <span>The pole star quality is calculated by <i>quality = 1 / (r * d<sup>n</sup>)</i> where r is the brighness rank of the star (for Polaris it is 50), d is the distance from the pole (for Polaris it is 0.75 degrees), and n is this simulation parameter.  This input is n, the exponent in the denominator of the quality formula.  Higher values of n give more value to being closer to the pole, thus resulting in dimmer stars being the best pole star.  Lower values of n give more value to being brighter than other stars (i.e. having a lower rank).</span>,
                        0,
                        6,
                        0.125
                    )}
                    {this.getRangeOption(
                        "rank",
                        "rank",
                        <span>This is the brightness rank of the pole star.  For Polaris, it is about 50.  In the night sky of earth, the brightest star is <a href="https://en.wikipedia.org/wiki/Sirius">Sirius</a>, so Sirius has a rank of 1.  Sirius is not near either pole, so it has a lower pole star quality than Polaris.</span>,
                        1,
                        200,
                        1
                    )}
                    {this.getRangeOption(
                        "distance_from_pole_degrees",
                        "distance from pole (degrees)",
                        <span>This is the angle between the celestial pole and the pole star.  This slider is in degrees (not radians), although it is converted to radians for the calculation.  As of 2020, Polaris is about 0.75 degrees from the north celestial pole.</span>,
                        0,
                        5,
                        0.05
                    )}
                    {this.getRangeOption(
                        "num_planets_to_simulate",
                        "number of planets to simulate",
                        <span>This is the number of planets to simulate.  If this value is higher, the simulation will take longer but the resulting number will be more accurate.</span>,
                        1,
                        50000,
                        1
                    )}
                    {this.getRangeOption(
                        "num_stars_limit",
                        "number of stars to simulate for each simulated planet",
                        <span>This is the number of stars to generate for each simulated planet.  The default is 5000 because about 5000 stars are visible to the unaided human eye on earth.  This number also contributes to the amount of time the simulation takes to complete.</span>,
                        1,
                        10000,
                        1
                    )}
                    
                    <PrettyBox>
                        include both poles<HelpContent
                        content="If checked, stars near either pole may be considered better pole stars than Polaris.  If unchecked, only stars near one pole (the north pole) may be considered better pole stars than Polaris."
                        /><br />
                        <input
                            type="checkbox"
                            onChange={event => {
                                this.setStateWithResultReset({
                                    include_both_poles: event.target.checked
                                });
                            }}
                            value={this.state.include_both_poles}
                        /><br />
                        {String(this.state.include_both_poles)}
                    </PrettyBox>

                    <br />

                    <button
                        style={{fontSize: 20}}
                        onClick={() => {this.runSimulation();}}
                    >Run Simulation</button>
                    <div style={{height: 100}}>
                        Probability of better pole star for {this.state.include_both_poles ? "either celestial pole" : "north celestial pole" }:<br />
                        {this.state.result === null
                            ? <br />
                            : (typeof this.state.result == typeof ""
                                ? this.state.result
                                : (<>
                                    By simulation: <span style={{fontSize: 20}}>{this.state.result.sim}</span><br />
                                    By calculation: <span style={{fontSize: 20}}>{this.state.result.calc}</span>
                                </>)
                            )
                        }
                    </div>
                </div>
            </div>
        );
    }
}