import React from 'react';

export default function SizeComparison({sunSize, moonSize, starColor}) {
  const PIXEL_RADIUS = 50; // pixels

  const divisor = Math.max(sunSize, moonSize); // some distance unit per pixel
  const moon_radius = PIXEL_RADIUS * moonSize / divisor; // pixels
  const sun_radius = PIXEL_RADIUS * sunSize / divisor; // pixels
  return (
    <div style={{
      backgroundColor: 'black',
      verticalAlign: 'middle',
      display: 'flex',
      width: 2 * PIXEL_RADIUS,
      height: 2 * PIXEL_RADIUS,
      align: 'right'
    }}>
      <div style={{
        width: PIXEL_RADIUS - moon_radius
      }} />
      <div style={{
        backgroundColor: 'gray',
        display: 'inline-block',
        height: 2 * moon_radius,
        width: moon_radius,
        margin: 'auto',
        borderTopLeftRadius: moon_radius,
        borderBottomLeftRadius: moon_radius
      }} />
      <div style={{
        backgroundColor: starColor || 'yellow',
        display: 'inline-block',
        height: 2 * sun_radius,
        width: sun_radius, 
        margin: 'auto',
        borderTopRightRadius: sun_radius,
        borderBottomRightRadius: sun_radius
      }} />
      <div style={{
        width: PIXEL_RADIUS - sun_radius
      }} />
    </div>
  );
}