import React from 'react';
import {Star, Planet, Moon, AstroObject} from './data.js';
import SizeComparison from './SizeComparison.js';

const MOON_PERIGEE_KM = 363228.9;
const MOON_APOGEE_KM = 405400;

function Comparison({moonDiameterRadians, sunDiameterRadians}) {
  return (
    <div style={{display: "flex", alignItems: "center"}}>
      <SizeComparison
        moonSize={moonDiameterRadians}
        sunSize={sunDiameterRadians}
      />
      <ul>
        <li>Moon angular diameter: {(moonDiameterRadians * 2 * 180 / Math.PI).toFixed(3)}°</li>
        <li>Star angular diameter: {(sunDiameterRadians * 2 * 180 / Math.PI).toFixed(3)}°</li>
        <li>Ratio: {(moonDiameterRadians / sunDiameterRadians).toFixed(3)}</li>
      </ul>
    </div>
  );
}

export default function ApparentSizeVariance() {
  const earth = new Planet('earth');
  const moon = new Moon('the moon');
  const sun = new Star('sun');
  const moon_radius_km = moon['radius (km)'].value;
  const sun_radius_km = sun['radius (km)'].value;
  const earth_radius_km = earth['equatorial radius (km)'].value;
  const earth_orbit = {
    semimajor_axis: AstroObject.get_base_unit_value_nicer(earth, "semimajor axis (10^6 km)"),
    perihelion: AstroObject.get_base_unit_value_nicer(earth, "perihelion (10^6 km)"),
    aphelion: AstroObject.get_base_unit_value_nicer(earth, "aphelion (10^6 km)")
  };
  const moon_orbit = {
    semimajor_axis: AstroObject.get_base_unit_value_nicer(moon, "semimajor axis (10^3 km)"),
    perigee: MOON_PERIGEE_KM,
    apogee: MOON_APOGEE_KM
  };
  const tangents = {
    moon: {
      maximum: moon_radius_km / (moon_orbit.perigee - earth_radius_km),
      minimum: moon_radius_km / moon_orbit.apogee,
      average: moon_radius_km / (moon_orbit.semimajor_axis - (earth_radius_km / 2))
    },
    sun: {
      maximum: sun_radius_km / earth_orbit.perihelion,
      minimum: sun_radius_km / earth_orbit.aphelion,
      average: sun_radius_km / earth_orbit.semimajor_axis
    }
  };
  const angles = Object.keys(tangents).reduce((acc, key) => {
    acc[key] = Object.keys(tangents[key]).reduce((acc2, key2) => {
      acc2[key2] = Math.atan(tangents[key][key2]);
      return acc2;
    }, {});
    return acc;
  }, {});
  return (<div>
    Maximum moon size to minimum sun size:
    <Comparison
      moonDiameterRadians={angles.moon.maximum}
      sunDiameterRadians={angles.sun.minimum}
    />
    Minimum moon size to maximum sun size:
    <Comparison
      moonDiameterRadians={angles.moon.minimum}
      sunDiameterRadians={angles.sun.maximum}
    />
    Average moon size to average sun size:
    <Comparison
      moonDiameterRadians={angles.moon.average}
      sunDiameterRadians={angles.sun.average}
    />

      
  </div>);



}