import React from 'react';
import {Planet, Star, Moon, AstroObject} from './data.js';
import {Line} from 'react-chartjs-2';

const MEASURED_RECESSION_RATE_CM_YR = 3.8;
const MOON_PERIGEE_KM = 363228.9;
const MOON_APOGEE_KM = 405400;
const MOON_SEMIMAJOR_AXIS_KM = 384400;

const EARTH_AGE_YRS = 4.5e9;

// The earth-moon barycenter is about 4600km from earth's center, about 3/4 of earth's radius.
// I'm not sure whether or not the moon's semimajor axis is the average distance from center of earth to center of moon.
// The average distance from center of earth to center of moon is 385,000km according to wikipedia.

function getMoonDistance(time_ago, exponent, current_distance, current_rate_of_recession) {
  // all must be in same units
  // assumes that d(semimajor axis)/dt = k / (semimajor axis)^exponent
  return Math.pow(
    Math.pow(current_distance, exponent + 1)
      + (exponent + 1) * Math.pow(current_distance, exponent) * current_rate_of_recession * (-time_ago)
    ,
    1 / (exponent + 1)
  );
}

function bisect(f, min, max) {
  // definitely not optimized for speed
  const ERROR_THRESHOLD = 0.000001;
  // const f_min = f(min);
  const f_max = f(max);
  let local_min = min;
  let local_max = max;
  let error = Infinity;
  let c = 0;
  while(error > ERROR_THRESHOLD && c < 1000) {
    const f_pivot = f((local_max + local_min) / 2);
    if(f_pivot * f_max > 0) {
      local_max = (local_max + local_min) / 2;
    } else {
      local_min = (local_max + local_min) / 2;
    }
    error = Math.abs((local_max - local_min) / local_min);
    // console.log(local_max, local_min, error);
    c++;
  }
  return (local_max + local_min) / 2;
}

function gyr_format(number, fixed_num_dp) {
  return (number/1e9).toFixed(fixed_num_dp || 1) + "B";
}

export default class RecessionOfMoon extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      distance_exponent: 0,
      recession_rate_cm_per_yr: MEASURED_RECESSION_RATE_CM_YR
    };
  }
  render() {
    const earth = new Planet('earth');
    const sun = new Star('sun');
    const moon = new Moon('the moon');
    const earth_semimajor_axis_km = AstroObject.get_base_unit_value_nicer(earth, 'semimajor axis (10^6 km)');
    // const sun_angular_radius = Math.atan(sun['radius (km)'].value / earth_semimajor_axis_km);
    const dist_at_which_moon_appears_same_size_as_sun_from_earth_center_km = (moon['radius (km)'].value * (earth_semimajor_axis_km / sun['radius (km)'].value));
    const dist_at_which_moon_appears_same_size_as_sun_from_earth_point_closest_to_moon_km = (moon['radius (km)'].value * (earth_semimajor_axis_km / sun['radius (km)'].value)) + earth["equatorial radius (km)"].value;
    const NUM_ENTRIES_FROM_FORMATION_TO_NOW = 16;
    const NUM_ENTRIES_IN_FUTURE = 11;
    const time_ago_yrs_array = Array(NUM_ENTRIES_FROM_FORMATION_TO_NOW + NUM_ENTRIES_IN_FUTURE).fill(0).map((_, index, array) => {
      return (NUM_ENTRIES_FROM_FORMATION_TO_NOW - 1 - index) * EARTH_AGE_YRS / (NUM_ENTRIES_FROM_FORMATION_TO_NOW - 1);
    });
    const recession_rate_km_per_yr = this.state.recession_rate_cm_per_yr / 100 / 1000;

    return (<div>
      <h3>Recession of moon and eclipses</h3>
      <p>It is widely accepted that the moon is getting farther from earth over billions of years.  The most well accepted recession rate is 3.8 centimeters per year.  However, this has been described as anomalously high.  This number comes from measurements in which a specially shaped mirror on the moon works with a laboratory on earth using a laser to measure distance.  But measurements from other fields, such as palientology and geology, indicate a slower recession rate.  Also, if the moon is receding at 3.8 centimeters per year, based on the physics of tidal recession, it would coincide with earth 1.5 billion years ago, much less than the generally accepted 4.5 billion year age of the earth-moon system. (<a href="https://link.springer.com/article/10.1186/2191-2521-1-1">source</a>).</p>
      <p>We live on earth in a window of time in which both total and annular eclipses are possible.  The graph below extrapolates the present recession rate relative to the age of the earth-moon system: {EARTH_AGE_YRS} years.  The rate of tidal recession can be modelled as k / a^n where k is a constant, a is the distance to the moon, and n is an integer.  So da/dt = k / a^n.  This differential equation solves to a = ((n+1) * (k*t + C)) ^ (1/(n+1)), where C is an arbitrary constant.  When trying to find the value of n, I could only find it from a <a href="https://en.wikipedia.org/wiki/Young_Earth_creationism">young-earth creationist</a> source (<a href="https://creation.com/the-moons-recession-and-age">https://creation.com/the-moons-recession-and-age</a>).  This source says n=6.</p>
      <p><strong>NOTE:</strong> the history of the moon's recession almost certainly does NOT look like the graph below!  It is not certain what it does look like, but it is well accepted that the recession rate was not constant in the past.  This extrapolation also ignores a lot of other parameters that could change (such as the eccentricity of the moon's orbit).</p>
      Slide these sliders to control some of the parameters.
      <div style={{textAlign:'center'}}>
        <input
          type="range"
          onChange={event => {
            this.setState({
              distance_exponent: Number(event.target.value)
            });
          }}
          value={this.state.distance_exponent}
          min="0"
          max="8"
          step="1"
        /><br />
        n={this.state.distance_exponent}
      </div><br />
      <div style={{textAlign:'center'}}>
        <input
          type="range"
          style={{cursor: 'pointer'}}
          onChange={event => {
            this.setState({
              recession_rate_cm_per_yr: Number(event.target.value)
            });
          }}
          value={this.state.recession_rate_cm_per_yr}
          min="0"
          max="6"
          step="0.05"
        /><br />
        present recession rate = {this.state.recession_rate_cm_per_yr} cm/yr
      </div>
      From point closest to moon:
      <ul>
        <li>
          Annular eclipses become possible {
            gyr_format(bisect(
              time_ago => dist_at_which_moon_appears_same_size_as_sun_from_earth_point_closest_to_moon_km - getMoonDistance(time_ago, this.state.distance_exponent, MOON_APOGEE_KM, recession_rate_km_per_yr),
              EARTH_AGE_YRS,
              -EARTH_AGE_YRS
            ), 3) 
          } years ago.<br />
        </li>
        <li>
          Total eclipses become impossible {
            gyr_format(bisect(
              time_ago => dist_at_which_moon_appears_same_size_as_sun_from_earth_point_closest_to_moon_km - getMoonDistance(time_ago, this.state.distance_exponent, MOON_PERIGEE_KM, recession_rate_km_per_yr),
              EARTH_AGE_YRS,
              -EARTH_AGE_YRS
            ), 3)
          } years ago.
        </li>
      </ul>
      From point at center of earth:
      <ul>
        <li>
          Annular eclipses become possible {
            gyr_format(bisect(
              time_ago => dist_at_which_moon_appears_same_size_as_sun_from_earth_center_km - getMoonDistance(time_ago, this.state.distance_exponent, MOON_APOGEE_KM, recession_rate_km_per_yr),
              EARTH_AGE_YRS,
              -EARTH_AGE_YRS
            ), 3)
          } years ago.
        </li>
        <li>
          Total eclipses become impossible {
            gyr_format(bisect(
              time_ago => dist_at_which_moon_appears_same_size_as_sun_from_earth_center_km - getMoonDistance(time_ago, this.state.distance_exponent, MOON_PERIGEE_KM, recession_rate_km_per_yr),
              EARTH_AGE_YRS,
              -EARTH_AGE_YRS
            ), 3)
          } years ago.
        </li>
      </ul>
      <Line
        data={{
          labels: time_ago_yrs_array.map(s => s.toFixed(0)),
          datasets: [
            {
              label: 'Distance at which the moon is the same size as the sun from center of earth',
              data: time_ago_yrs_array.map(_ => dist_at_which_moon_appears_same_size_as_sun_from_earth_center_km),
              pointBackgroundColor: 'brown',
              pointBorderColor: 'brown',
              borderColor: 'brown',
              lineTension: 0,
              fill: false
            },
            {
              label: 'Distance at which the moon is the same size as the sun from point on earth closest to moon',
              data: time_ago_yrs_array.map(_ => dist_at_which_moon_appears_same_size_as_sun_from_earth_point_closest_to_moon_km),
              pointBackgroundColor: 'green',
              pointBorderColor: 'green',
              borderColor: 'green',
              lineTension: 0,
              fill: false
            },
            {
              label: 'Moon apogee',
              data: time_ago_yrs_array.map(time_ago_yrs => {
                const moon_apogee = getMoonDistance(time_ago_yrs, this.state.distance_exponent, MOON_APOGEE_KM, recession_rate_km_per_yr);
                return moon_apogee;
              }),
              pointBackgroundColor: 'black',
              pointBorderColor: 'black',
              borderColor: 'black',
              lineTension: 0,
              fill: false
            },
            {
              label: 'Moon perigee',
              data: time_ago_yrs_array.map(time_ago_yrs => {
                const moon_perigee = getMoonDistance(time_ago_yrs, this.state.distance_exponent, MOON_PERIGEE_KM, recession_rate_km_per_yr);
                return moon_perigee;
              }),
              pointBackgroundColor: 'black',
              pointBorderColor: 'black',
              borderColor: 'black',
              lineTension: 0,
              fill: false
            },
            {
              label: 'Moon semimajor axis',
              data: time_ago_yrs_array.map(time_ago_yrs => {
                const moon_perigee = getMoonDistance(time_ago_yrs, this.state.distance_exponent, MOON_SEMIMAJOR_AXIS_KM, recession_rate_km_per_yr);
                return moon_perigee;
              }),
              pointBackgroundColor: 'purple',
              pointBorderColor: 'purple',
              borderColor: 'purple',
              lineTension: 0,
              fill: false
            }
          ]
        }}
        options={{
          plugins: {
            title: {
              display: true,
              text: 'Extrapolation of Moon Recession Compared to Eclipse Critical Distances'
            }
          },
          scales: {
            y: {
              beginAtZero: true,
              title: {
                text: "Moon distance from earth (kilometers)",
                display: true
              }
            },
            x: {
              grid: {
                color: time_ago_yrs_array.map(t => t === 0 ? 'black' : 'rgb(175,175,175)'),
                lineWidth: time_ago_yrs_array.map(t => t === 0 ? 2 : 1)
              },
              ticks: {
                callback: idx => {
                  const years_before_present = time_ago_yrs_array[idx];
                  return (
                    String(years_before_present) === String(EARTH_AGE_YRS)
                    ? gyr_format(EARTH_AGE_YRS) + " (age)"
                    : (String(years_before_present) === "0"
                      ? "0 (now)"
                      : gyr_format(years_before_present)
                    )
                  );
                },
              },
              title: {
                text: "Years before present",
                display: true
              }
            }
          }
        }}
      />
    </div>);
  }
}